


























import Vue from 'vue'
import Modal from '@/components/Modal.vue'

export default Vue.extend({
	components: {
		Modal,
	},

	data() {
		return {
			isTitleEdit: false,
			isDescEdit: false,
			inputTitle: '',
			inputDesc: '' as string | null,
		}
	},

	computed: {
		card() {
			return this.$store.state.card
		},
		bid() {
			return this.$route.params.bid
		},
		cid() {
			return this.$route.params.cid
		},
	},

	methods: {
		onTitleEdit() {
			this.isTitleEdit = true
		},
		onDescEdit() {
			this.isDescEdit = true
		},
		onSubmitTitle() {
			const inputTitle = this.inputTitle.trim()
			if (inputTitle === this.card.title) return
			const cid = this.card.id
			const data = {
				listId: this.card.listId,
				title: inputTitle,
			}
			this.$store.dispatch('UPDATE_CARD', { cid, data })
		},
		onSubmitDesc() {
			if (this.inputDesc === null) return
			const inputDesc = this.inputDesc.trim()
			if (inputDesc === this.card.description) return
			const cid = this.card.id
			const data = {
				listId: this.card.listId,
				description: inputDesc,
			}
			this.$store.dispatch('UPDATE_CARD', { cid, data })
		},
	},

	created() {
		this.$store.dispatch('FETCH_CARD', this.cid).then(() => {
			this.inputTitle = this.card.title
			this.inputDesc = this.card.description
		})
	},
})
